<template>
    <div class="member">
        <div class="member-nav">
           <img @click='prev' src="./img/left.png">
           <span class="member-span">会员中心</span>
       </div>
       <div v-show="s">
         <p class="member-p">为账户 <span v-if='bol'>{{username}}</span> 购买会员</p>
        <div class="member-div">
          <router-link to="/mine/member/privilege">
            <span>会员特权</span>
            <span class="mem-span">会员说明</span>
            <img src="./img/箭头_右.png">
          </router-link>
          <div class="money">
              <img width="37" height="42" src="./img/省钱.png">
              <div class="money-div">
                <h4>减免配送费</h4>
                <p>每月减免30单，每日可减免3单，每单最高减4元</p>
                <p>蜂鸟专送专享</p>
              </div>
          </div>
           <div class="money">
              <img width="37" height="42" src="./img/减免.png">
              <div class="money-div">
                <h4>减免配送费</h4>
                <p>每月减免30单，每日可减免3单，每单最高减4元</p>
                <p>蜂鸟专送专享</p>
              </div>
          </div>
        </div>
        <div class="dredge">
            <div class="dredge-div">
              <span>开通会员</span>
            </div>
            <div class="purchase">
              <span>1个月</span>
              <span>￥20</span>
              <router-link to="/mine/member/payment">
                <div>购买</div>
              </router-link>
            </div>
        </div>
        <div class="conversion">
          <router-link to="/mine/member/exchange">
            <span>兑换会员</span>
            <span class="conversion-span">使用卡号卡密</span>
            <img src="./img/箭头_右.png">
          </router-link>
        </div>
        <div class="conversion">
          <router-link to="/mine/member/history">
            <span>购买记录</span>
            <span class="conversion-span">开发票</span>
            <img src="./img/箭头_右.png">
          </router-link>
        </div>
       </div>
    </div>
    
</template>

<script>
export default {
  name: "member",
  data() {
    return {
      username:"",
      s:""
    };
  },
  methods: {
    prev() {
      this.$router.go(-1);
    }
  },
  computed:{
     bol(){
      if(localStorage.obj!==""){
        return true;
      }else{
        return false;
      }
    }
  },
  created(){

       let at = this.$store.state.username;
       console.log(at);
       if(at !== ""){
         this.s = true;
        }else if(at == ""){
          this.s = false;
       }

      //  let str=localStorage.obj;
      //  let obj=  str?  JSON.parse(str):{};
       this.username= at;
  }
};
</script>

<style scoped>
.member {
  background: #f5f5f5;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.member-nav {
  width: 100%;
  height: 0.57rem;
  font-size: 0.25rem;
  background: #3190e8;
  z-index: 4;
  text-align: center;
  position: relative;
}
.member-nav > img {
  height: 0.34rem;
  float: left;
  color: white;
  position: absolute;
  top: 50%;
  left: 1.5%;
  transform: translateY(-50%);
}
.member-span {
  color: white;
  display: inline-block;
  font-weight: 550;
  line-height: 0.56rem;
}
.member-p{
  display: inline-block;
  height: 0.58rem;
  padding-left: 0.21rem;
  line-height: 0.58rem;
  font-size: 0.18rem;
}
.member-p span{
  font-size: 0.18rem;
  font-weight: 600;
}
.member-div{
  background: white;
  padding-left: 0.21rem;
  margin-bottom: 0.138rem;
}
.member-div a{
  height: 0.58rem;
  display: block;
  border-bottom: 1px solid #F5F5F5;
  padding-right: 0.21rem;
  line-height: 0.58rem;
  position: relative;
}
.member-div a span{
  height: 0.58rem;
  display: inline-block;
  color: #333333;
  font-weight: 200;
}
.mem-span{
  font-size: 0.17rem;
  color: #B5B5B5;
  position: absolute;
  right: 0.46rem;
  top: 0;
}
.member-div a img{
  height: 0.25rem;
  position: absolute;
  right: 0.21rem;
  top: 0.15rem;
}
.money{
  height: 0.663rem;
  padding-top: 0.235rem;
  padding-bottom: 0.235rem;
  position: relative;
  border-bottom: 1px solid #f5f5f5;
}
.money img{
  position: absolute;
  left: 0rem;
  top: 0.235rem;
}
.money-div{
  font-size: 0.15rem;
  color: #B1B1B1;
  position: absolute;
  left: 0.5rem;
  top: 0.235rem;
  font-weight: 200;
}
.money-div h4{
   font-size: 0.2rem;
   color: #333333;
}
.dredge{
  background: white;
  padding-left: 0.21rem;
  margin-bottom: 0.138rem;
}
.dredge-div{
   height: 0.58rem;
  display: block;
  border-bottom: 1px solid #F5F5F5;
  padding-right: 0.21rem;
  line-height: 0.58rem;
}
.dredge-div span{
  height: 0.58rem;
  display: inline-block;
  color: #333333;
  font-weight: 200;
}
.purchase{
  height: 0.75rem;
  line-height: 0.75rem;
  padding-right: 0.2rem;
  position: relative;
}
.purchase div{
  width: 0.74rem;
  height: 0.34rem;
  text-align: center;
  line-height: 0.34rem;
  display: inline-block;
  border: 0.01rem solid chocolate;
  border-radius: 0.06rem;
  color: chocolate;
  position: absolute;
  right: 0.21rem;
  top: 0.2rem;
}
.conversion{
  background: white;
  margin-bottom: 0.138rem;
}
.conversion a{
  height: 0.58rem;
  display: block;
  border-bottom: 1px solid #F5F5F5;
  padding-left: 0.21rem;
  padding-right: 0.21rem;
  line-height: 0.58rem;
  position: relative;
}
.conversion a span{
  height: 0.58rem;
  display: inline-block;
  color: #333333;
  font-weight: 200;
}
.conversion-span{
  font-size: 0.17rem;
  color: #B5B5B5;
  position: absolute;
  right: 0.46rem;
  top: 0;
}
.conversion a img{
  height: 0.25rem;
  position: absolute;
  right: 0.21rem;
  top: 0.15rem;
}
</style>


