export default{
    //context:上下文， 类似store对象
    //{commit} = context 参考--> {name} = {name:"小虎"}
    asyReduce({ commit }, n) {
        //触发mutation函数
        setTimeout(() => {
            //context.commit('reduce',n);
            commit('reduce', n);
        }, 1000);
    }
}


