<template>
    <div class="forget">
        <div class="forget-nav">
            <img @click='prev' src="./img/left.png">
            <span class="forget-span">重置密码</span>
      </div>
      <div class="forget-inputs">
            <div class="forget-input_container">
                <input type="text" v-model="username" class="forget-search_input" placeholder="账号">
            </div>
            <div class="forget-input_container">
                <input type="text" v-model="oldpassWord" class="forget-search_input" placeholder="旧密码">
            </div>
             <div class="forget-input_container">
                <input type="text" v-model="newpassword" class="forget-search_input" placeholder="请输入新密码">
            </div>
             <div class="forget-input_container">
                <input type="text" v-model="confirmpassword" class="forget-search_input" placeholder="请确认密码">
            </div>
            <div class="forget-input_container">
                <input type="text" v-model="codeNumer" class="forget-search_input" placeholder="验证码">
                <img @click="getCode()" :src="code&&code" class="forget-login_img">
                <div class="forget-change_img">
                  <p class="forget-img_p1" @click="getCode()">看不清</p>
                  <p class="forget-img_p2" @click="getCode()">换一张</p>
                </div>
            </div>
        </div>
        <el-button type="success forget-login_button" @click="getClick()">确认修改</el-button>
    </div>
</template>

<script>
export default {
  data() {
    return {
      value: true,
      code: "",
      codeNumer: "",
      username :"",
      oldpassWord :"",
      newpassword :"",
      confirmpassword :"",
      captcha_code : "",
      value1: true,
      value2: true
    };
  },
  created() {
    this.getCode();
  },
  methods: {
    prev() {
      this.$router.go(-1);
    },
    getCode() {
      this.axios({
        method: "post",
        url: "https://elm.cangdu.org/v1/captchas",
        withCredentials: true // 默认false
      }).then(res => {
        if (res.data.status == 200) {
        }
        this.code = res.data.code;
      });
    },
    getClick(){
        this.axios({
            method:"post",
            url:"https://elm.cangdu.org/v2/changepassword",
            withCredentials: true,
            data: {
            captcha_code: this.codeNumer,
            username: this.username,
            oldpassWord: this.oldpassWord,
            newpassword: this.newpassword,
            confirmpassword: this.confirmpassword
            }
        }).then(res =>{
            console.log(res);
            if(res.data.status == 1){
                alert(res.data.success);
                this.$router.push({name:"Mine"});
            }else{
                alert(res.data.message);
            }
        })
    }
  }
};
</script>

<style scoped>
.forget {
  background: #f5f5f5;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.forget-nav {
  width: 100%;
  height: 0.57rem;
  font-size: 0.25rem;
  background: #3190e8;
  z-index: 4;
  text-align: center;
  position: fixed;
  top: 0;
  left: 0;
}
.forget-nav > img {
  height: 0.34rem;
  float: left;
  color: white;
  position: absolute;
  top: 50%;
  left: 1.5%;
  transform: translateY(-50%);
}
.forget-span {
  color: white;
  display: inline-block;
  font-weight: 550;
  line-height: 0.56rem;
}
.forget-inputs {
  width: 100%;
  height: 3.07rem;
  background-color: white;
  margin-top: 0.75rem;
}
.forget-input_container {
  width: 90%;
  height: 0.25rem;
  padding: 0.18rem 0.23rem;
  border-bottom: 0.01rem solid gray;
  position: relative;
}
.forget-search_input {
  width: 50%;
  height: 0.25rem;
  font-size: 0.25rem;
  line-height: 0.25rem;
  border: 0px;
  outline: none;
}
.forget-login_img {
  position: absolute;
  top: 30%;
  right: 20%;
}
.forget-change_img {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 0.2rem;
}
.forget-login_button {
  position: absolute;
  width: 92%;
  top: 4rem;
  left: 50%;
  transform: translateX(-50%);
  padding-left: 0.2rem;
  font-size: 0.2rem;
}
</style>


