<template>
  <div class="city">
    <div class="header_top">
        <i @click='prev' class="el-icon-arrow-left"></i>
      <span class="header_cityname">{{cityname}}</span>
      <div class="head_right">
        <router-link to="/home" class="login">
          <span class="login_span">切换城市</span>
        </router-link>
      </div>
    </div>
    <div class="city_all">
      <el-input v-model="input" class="city_input" placeholder="输入学校、商务楼、地址"></el-input>
      <el-button type="primary" class="city_button" @click='postpois'>提交</el-button>
    </div>
    <div class="city_bottom">
      <span class="city_span">搜索历史</span>
      <ul class="city_ul">
            <li v-for="(item, index) in placelist" @click='nextpage(index, item.geohash)' :key="index">
                <h4 class="city_h4">{{item.name}}</h4>
                <p class="city_p">{{item.address}}</p>
            </li>
             <footer v-if="historytitle&&placelist.length" class="clear_all_history" @click="clearAll">清空所有</footer>
      </ul>
       
      <div class="search_none_place" v-if="placeNone">很抱歉！无搜索结果</div>
      

    </div>
  </div>

</template>

<script type="text/ecmascript-6">
import {getStore, setStore, removeStore} from '../../../config/localhost';
export default {
  name: "city",
  data() {
    return {
       inputVaule: "", // 搜索地址
       input: "",
       cityid: "", //当前城市id;
       cityname: "", //当前城市名字;
       placeHistory:[], // 历史搜索记录
       placelist: [] ,// 搜索城市列表
       historytitle: true, // 默认显示搜索历史头部，点击搜索后隐藏
      placeNone: false, // 搜索无结果，显示提示信息
    };
  },

  created() {
    this.cityid = this.$route.params.cityid;
    var url = 'https://elm.cangdu.org/v1/cities/'+this.cityid ;

    this.axios.get(url).then(cityid => {
      //成功后的回调；
      this.cityname = cityid.data.name;
    });
  },
  methods: {
     prev() {
      this.$router.go(-1);
    },
     initData(){
                //获取搜索历史记录
                if (getStore('placeHistory')) {
                    this.placelist = JSON.parse(getStore('placeHistory'));
                }else{
                    this.placelist = [];
                }
            },
    postpois() {
     
      var apii = 'https://elm.cangdu.org/v1/pois' + "?city_id=" + this.cityid + "&keyword=" + this.input;
      this.axios.get(apii).then(res => {
        this.placelist=res.data;
        this.placeNone = res.length? false : true;
      });
    },
     nextpage(index, geohash){
                let history = getStore('placeHistory');
                let choosePlace = this.placelist[index];
                if (history) {
                    let checkrepeat = false;
                    this.placeHistory = JSON.parse(history);
                    this.placeHistory.forEach(item => {
                        if (item.geohash == geohash) {
                            checkrepeat = true;
                        }
                    })
                    if (!checkrepeat) {
                        this.placeHistory.push(choosePlace)
                    }
                }else {
                    this.placeHistory.push(choosePlace)
                }
                setStore('placeHistory',this.placeHistory)
                this.$router.push({path:'/home/msite',query:{geohash}})
            },
     clearAll(){
                removeStore('placeHistory');
                this.initData();
            }
  },
   
  mounted() {
     this.initData();
     
  },
  components: {}
};
</script>

<style scoped>
/*顶部 */
.city {
  font-family: Microsoft Yahei;
  background-color: rgb(245, 245, 245);
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.header_top {
  width: 100%;
  height: 0.56rem;
  background-color: rgb(49, 144, 232);
  position: fixed;
  top: 0;
  left: 0;
  overflow: hidden;
  z-index: 100;
}
.el-icon-arrow-left {
  position: absolute;
  color: white;
  left: 0.1rem;
  top: 50%;
  transform: translateY(-50%);
  font-size: 0.3rem;
  clear: both;
}
/* 顶部登录*/
.login {
  position: absolute;
  float: right;
  right: 0.2rem;
  top: 50%;
  transform: translateY(-50%);
  clear: both;
}
.login_span {
  color: white;
  font-size: 0.19rem;
  font-weight: 400;
}
.header_cityname {
  position: absolute;
  color: white;
  font-size: 0.25rem;
  line-height: 0.5rem;
  font-weight: bold;
  margin-left: 50%;
  top: 0;
  left:0;
  transform: translateX(-50%);
}
/*查找位置*/
.city_all {
  width: 100%;
  height: 1.4rem;
  position: fixed;
  top: 0.6rem;
  left: 0;
  background-color: white;
  border-top: 1px solid rgb(228, 228, 228);
  border-bottom: 2px solid rgb(228, 228, 228);
}
.city_input {
  display: block;
  width: 90%;
  height: 1rem;
  position: absolute;
  top: 0.2rem;
  left: 50%;
  transform: translateX(-50%);
}
.city_button {
  width: 90%;
  height: 0.4rem;
  line-height: 0.05rem;
  margin-top: 0.25rem;
  position: absolute;
  top: 0.6rem;
  left: 50%;
  transform: translateX(-50%);
}
/*搜索历史*/
.city_bottom {
  position: fixed;
  width: 100%;
  height: 0.3rem;
  top: 2rem;
  border-bottom: 2px solid rgb(228, 228, 228);
}
.city_span {
  font-size: 0.15rem;
  margin-left: 0.15rem;
  line-height: 0.3rem;
}

/*ul*/
.city_ul{
  position: fixed;
  width: 100%;
  top: 2.3rem;
  border-top:1px solid  rgb(228, 228, 228);
  overflow: hidden;
  background-color: white;
  
}
.city_li{
  width:100%;
  height:0.9rem;
  padding-top:0.1rem;
  border-bottom:1px solid  rgb(228, 228, 228);
 

  
}
.city_h4{
  width:90%;
  margin:0 auto;
  padding-top:0.2rem;
  font-size:0.22rem;
  color:black;

}
.city_p{
  width:90%;
  margin:0 auto;
  font-size:0.15rem;
  padding-top:0.1rem;
  color:rgb(153,153,153);
}
.clear_all_history{
  
  background-color: white;
  font-size:0.2rem;
  text-align:center;
  line-height: 0.6rem;
  margin-top:0.2rem;
  border-top:2px solid rgb(228, 228, 228);
  border-bottom:2px solid rgb(228, 228, 228);

}
.search_none_place{
  padding-top:0.2rem;
  text-align: center;
}
</style>



