<template>
  <div class="info">
    <div class="header_top">
      <router-link to="/home/mine">
        <i class="el-icon-arrow-left"></i>
      </router-link>
      <span class="header_info">账户信息</span>
    </div>
    <div class="header_login">
      <div class="info_upload">
        <span>头像</span>
        <img src="" alt="">
        <i class="el-icon-arrow-right"></i>
      </div>
      <router-link to="###" class="info_username">
        <span class="username_span1">用户名</span>
        <span class="username_span2" >{{username}}</span>
        <i class="el-icon-arrow-right"></i>
      </router-link>
      <router-link to="/mine/info/address" class="info_address">
        <span>收货地址</span>
        <i class="el-icon-arrow-right"></i>
      </router-link>
    </div>
    <div class="info_center">
      <span>账号绑定</span>
      <router-link to="###" class="info_centerall">
        <img src="../img/手机.png" alt="">
        <span>手机</span>
        <i class="el-icon-arrow-right"></i>
      </router-link>
    </div>
    <div class="info_safety">
      <span class="info_safety_span">安全设置</span>
      <router-link to="/forget" class="safety_a">
        <span class="safety_span1">登录密码</span>
        <span class="safety_span2">修改</span>
        <i class="el-icon-arrow-right"></i>
      </router-link>
    </div>
    <router-link tag="div" to="/home/mine" class="safety_bottom">
      <el-button type="danger" class="safety_button" @click="outLogin">退出登录</el-button>
    </router-link>

  </div>
</template>

<script type="text/ecmascript-6">
export default {
  name: "info",
  data() {
    return {
      username: ""
    };
  },
  created() {
       this.username=this.$store.state.username;
  },
 
  methods: {
    outLogin() {
      localStorage.obj = "";
      
     location.reload();
    }
  }
};
</script>

<style scoped>
/*顶部 */
.info {
  font-family: Microsoft Yahei;
  background-color: rgb(245, 245, 245);
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.header_top {
  width: 100%;
  height: 0.5rem;
  background-color: rgb(49, 144, 232);
  position: fixed;
  top: 0;
  left: 0;
  overflow: hidden;
  z-index: 100;
}
.el-icon-arrow-left {
  position: absolute;
  color: white;
  left: 0.1rem;
  top: 50%;
  transform: translateY(-50%);
  font-size: 0.3rem;
  clear: both;
}
.header_info {
  position: absolute;
  color: white;
  font-size: 0.25rem;
  line-height: 0.5rem;
  font-weight: bold;
  margin-left: 50%;
  top: 0;
  left: 0;
  transform: translateX(-50%);
}

/*上部结构*/
.header_login {
  position: absolute;
  top: 0.5rem;
  left: 0;
  margin-top: 0.2rem;
  border-top: 1px solid rgb(228, 228, 228);
  border-top: 1px solid rgb(228, 228, 228);
  width: 100%;
  height: 2.1rem;
  background-color: white;
}
.info_upload {
  position: absolute;
  top: 0;
  width: 100%;
  height: 0.9rem;
  border-bottom: 1px solid rgb(228, 228, 228);
}
.info_upload span {
  padding-left: 0.2rem;
  line-height: 0.9rem;
  font-size: 0.18rem;
}
.el-icon-arrow-right {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 0.2rem;
}
.info_username {
  position: absolute;
  width: 100%;
  height: 0.6rem;
  top: 0.9rem;
  border-bottom: 1px solid rgb(228, 228, 228);
  text-decoration: none;
  color: black;
}
.username_span1 {
  padding-left: 0.2rem;
  line-height: 0.6rem;
  font-size: 0.18rem;
}
.username_span2 {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 0.5rem;
  height: 0.4rem;
  line-height: 0.4rem;
  padding: 0.2rem;
  font-size: 0.2rem;
  color: rgb(102, 102, 102);
}
.info_address {
  position: absolute;
  width: 100%;
  height: 0.6rem;
  top: 1.5rem;
  border-bottom: 1px solid rgb(228, 228, 228);
  text-decoration: none;
  color: black;
}
.info_address span {
  padding-left: 0.2rem;
  line-height: 0.6rem;
  font-size: 0.18rem;
}
.info_center {
  position: absolute;
  top: 2.8rem;
  width: 100%;
  height: 1rem;
  /* border: 1px solid red; */
}
.info_center span {
  height: 0.4rem;
  line-height: 0.4rem;
  padding: 0.2rem;
  font-size: 0.1rem;
  color: rgb(102, 102, 102);
}
.info_centerall {
  position: absolute;
  top: 0.4rem;
  left: 0;
  width: 100%;
  height: 0.6rem;
  border-top: 1px solid rgb(228, 228, 228);
  border-bottom: 1px solid rgb(228, 228, 228);
  background-color: white;
}
.info_centerall img {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 0.2rem;
  width: 0.25rem;
  height: 0.3rem;
  line-height: 0.6rem;

  background-color: rgb(16, 174, 255);
}
.info_centerall span {
  height: 0.6rem;
  line-height: 0.6rem;
  padding-left: 0.6rem;
  font-size: 0.18rem;
  color: black;
}
.info_safety {
  position: absolute;
  top: 3.8rem;
  width: 100%;
  height: 1rem;
}
.info_safety_span {
  height: 0.4rem;
  line-height: 0.4rem;
  padding: 0.2rem;
  font-size: 0.1rem;
  color: rgb(102, 102, 102);
}
.info_safety a {
  position: absolute;
  top: 0.4rem;
  left: 0;
  width: 100%;
  height: 0.6rem;
  border-top: 1px solid rgb(228, 228, 228);
  border-bottom: 1px solid rgb(228, 228, 228);
  background-color: white;
}
.safety_span1 {
  height: 0.6rem;
  line-height: 0.6rem;
  padding: 0.2rem;
  font-size: 0.18rem;
  color: black;
}
.safety_span2 {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 0.3rem;
  height: 0.4rem;
  line-height: 0.4rem;
  padding: 0.2rem;
  font-size: 0.2rem;
  color: rgb(102, 102, 102);
}

.safety_button {
  position: absolute;
  width: 90%;
  height: 0.4rem;
  line-height: 0.15rem;
  top: 5.3rem;
  left: 50%;
  transform: translateX(-50%);
}
</style>



