               <span :class="{'activity_show':!isclass}" @click="toyu($event)">商家代金卷</span>
           </div>
           <div v-if="isclass">
            <div class="packet-k">
             <div class="packet-kims1">
                  <span>有<span>3</span>个红包即将到期</span>
                 <router-link to="/mine/packet/HbDescription">
                     <span style="color: #3190e8;
    font-size: 0.17rem;"><img height="15" src="./img/问号.png">红包说明</span>
                 </router-link>
             </div>
            <ul class="hongbao_list_ul">
                <li class="hongbao_list_li">
                    <section class="list_item">
                        <div class="list_item_left">
                            <span class="sq">¥</span> 
                            <span class="sw">1</span> 
                            <span class="sq">.</span> 
                            <span class="sq">0</span> 
                            <p>满 20 元可用</p>
                        </div> 
                        <div class="list_item_right">
                            <h4>分享红包</h4> 
                            <p>2017-05-23到期</p> 
                            <p>限收货手机号为 13681711254</p>
                        </div> 
                        <div class="time_left">剩3日</div>
                    </section>
                </li>
                <li class="hongbao_list_li">
                    <section class="list_item">
                        <div class="list_item_left">
                            <span class="sq">¥</span> 
                            <span class="sw">2</span> 
                            <span class="sq">.</span> 
                            <span class="sq">0</span> 
                            <p>满 40 元可用</p>
                        </div> 
                        <div class="list_item_right">
                            <h4>分享红包</h4> 
                            <p>2017-05-23到期</p> <p>限收货手机号为 13681711254</p>
                        </div> 
                        <div class="time_left">剩3日</div>
                    </section>
                </li>
