<script>
export default {
  name: "packet",
  data(){
      return{
          isclass:true,
          s:""
      }
  },
  methods: {
    prev() {
      this.$router.go(-1);
    },
    toyu(e){
        if (e.target.className == "activity_show") {
        this.isclass = this.isclass;
      } else {
        this.isclass = !this.isclass;
      }
    }

  },
  created() {
      let at = this.$store.state.username;
       console.log(at);
       if(at !== ""){
         this.s = true;
        }else if(at == ""){
          this.s = false;
       }
  }
};
</script>

