<template>
    <div class="exchange1">
        <div class="exchange1-nav">
           <img @click='prev' src="./img/left.png">
           <span class="exchange1-span">兑换红包</span>
       </div>
       <div class="exc">
           <div class="input_container">
                <input type="text" class="search_input" placeholder="兑换码">
            </div>
            <div class="input_container">
                <input type="text" v-model="codeNumer" class="search_input" placeholder="验证码">
                <img @click="getCode()" :src="code&&code" class="login_img">
                <div class="change_img">
                  <p class="img_p1" @click="getCode()">看不清</p>
                  <p class="img_p2" @click="getCode()">换一张</p>
                </div>
            </div>
            <div class="determine">兑换</div>
       </div>
    </div>
</template>

<script>
export default {
    data(){
        return{
            codeNumer: "",
            code: ""
        }
    },
     created() {
    this.getCode();
  },
     methods: {
    prev() {
      this.$router.go(-1);
    },
     getCode() {
      this.axios({
        method: "post",
        url: 'https://elm.cangdu.org/v1/captchas',
        withCredentials: true // 默认false
      }).then(res => {
        if (res.data.status == 200) {
        }
        this.code = res.data.code;
      });
    }
    }
   
};
</script>

<style scoped>
.exchange1 {
  background: #f5f5f5;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.exchange1-nav {
  width: 100%;
  height: 0.57rem;
  font-size: 0.25rem;
  background: #3190e8;
  z-index: 4;
  text-align: center;
  position: relative;
}
.exchange1-nav > img {
  height: 0.34rem;
  float: left;
  color: white;
  position: absolute;
  top: 50%;
  left: 1.5%;
  transform: translateY(-50%);
}
.exchange1-span {
  color: white;
  display: inline-block;
  font-weight: 550;
  line-height: 0.56rem;
}
.input_container {
  width: 90%;
  height: 0.25rem;
  margin-top: 0.17rem;
  border-radius: 0.05rem;
  padding: 0.18rem 0.23rem;
  position: relative;
  background: white;
}
.search_input {
  width: 50%;
  height: 0.25rem;
  font-size: 0.25rem;
  line-height: 0.25rem;
  border: 0px;
  outline: none;
}
.login_img {
  position: absolute;
  top: 30%;
  right: 20%;
}
.switch {
  float: right;
}
.change_img {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 0.2rem;
}
.determine{
    margin: 0.21rem;
    margin-bottom: 0;
    height: 0.52rem; 
    text-align: center;
    line-height: 0.52rem;
    background: #CCCCCC;
    border-radius: 0.05rem;
}
</style>



