<template>
    <div class="address">
        <div class="address-nav">
          <router-link to='/mine/info'>
               <img src="./img/left.png">
          </router-link> 
           <span class="address-span">编辑地址</span>
           <span class="address-span1" @click="tui()" v-show="s">编辑</span>
           <span class="address-span1" @click="tui()" v-show="!s">完成</span>
       </div>
        <div class="address-div1">
          <ul>
            <li v-for="(item,index) in data" :key="index">
                <p>{{item.address_detail}}</p><br>
                <p>{{item.phone}}</p>
                <span v-show="!s" @click="shan(index)">x</span>
            </li>
          </ul>
        </div>
       <router-link to="/mine/info/address/add">
           <div class="address-div">
            <span>新增地址</span>
            <img src="./img/箭头_右.png">
       </div>
       </router-link>
    </div>
</template>

<script>
export default {
  data(){
    return{
      data:[],
      s:true
    }
  },
  created() {

    var zhuij = this.$store.state.userInfo;
    console.log(zhuij);
    let api = `https://elm.cangdu.org/v1/users/${zhuij.id}/addresses`;
    this.$http.get(api).then(data => {
    this.data = data.data;
    console.log(data);
    },(res)=>{
      console.log(res);
    })
  },
  methods: {
    tui(){
      this.s = !this.s;
    },
    shan(index){
      console.log(index);
      this.data.splice(index,1);
    }
  }
};
</script>

<style scoped>
.address {
  background: #f5f5f5;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.address-nav {
  width: 100%;
  height: 0.57rem;
  font-size: 0.25rem;
  background: #3190e8;
  z-index: 4;
  text-align: center;
  overflow: hidden;
  position: relative;
}
.address-nav img {
  height: 0.34rem;
  float: left;
  color: white;
  position: absolute;
  top: 50%;
  left: 1.5%;
  transform: translateY(-50%);
}
.address-span {
  color: white;
  display: inline-block;
  font-weight: 550;
  line-height: 0.56rem;
}
.address-span1 {
  color: white;
  display: inline-block;
  font-weight: 550;
  line-height: 0.56rem;
  float: right;
  margin-right: 0.15rem;
}
.address-div {
  background: white;
  height: 0.41rem;
  line-height: 0.41rem;
  display: flex;
  justify-content: space-between;
  padding: 0.06rem 0.12rem;
  margin-top: 0.12rem;
  border-top: 1px solid #cccccc; 
  color: gray;
}
.address-div img{
    height: 0.3rem;
}
.address-div1 li{
  background: white;
  margin-top: 0.15rem;
  padding: 0.13rem;
  overflow: hidden;
}
.address-div1 li p{
  height: 0.25rem;
  line-height: 0.25rem;
  display: inline;
}
.address-div1 li span{
  display: inline;
  float: right;
  font-size: 0.25rem;
}
</style>


