<template>
  <div class="download">
      <div class="download-nav">
        <img @click='prev' src="./img/left.png">
        <span class="download-span">下载</span>
      </div>
      <div class="download-div">
          <img src="./img/download.png">
          <p>下载饿了么APP</p>
          <div @click="teg">下载</div>
      </div>
      <div v-show="t" class="tip">
           <img src="./img/感叹号.png">
           <p class="tip_text">IOS用户请前往AppStore下载</p>
           <div @click="tet" class="confrim">确认</div>
       </div>
  </div>
</template>

<script>
export default {
  name:"download",
  data(){
    return{
      t:false
    }
  },
  methods:{
       prev(){
        this.$router.go(-1)
        },
         teg(){
        this.t = true;
        },
        tet(){
        this.t = false; 
        }
    }
}
</script>

<style scoped>
.download {
  background: white;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.download-nav {
  width: 100%;
  height: 0.57rem;
  font-size: 0.25rem;
  background: #3190e8;
  z-index: 4;
  text-align: center;
  position: fixed;
  top: 0;
  left: 0;
}
.download-nav > img {
  height: 0.34rem;
  float: left;
  color: white;
  position: absolute;
  top: 50%;
  left: 1.5%;
  transform: translateY(-50%);
}
.download-span {
  color: white;
  display: inline-block;
  font-weight: 550;
  line-height: 0.56rem;
}
.download-div {
  margin-top: 0.57rem;
  text-align: center;
}
.download-div img {
  height: 2.25rem;
  border-radius: 0.3rem;
  margin-top: 0.2875rem;
}
.download-div p {
  font-size: 0.24rem;
  margin-top: 0.07rem;
  margin-bottom: 0.288rem;
}
.download-div div {
  height: 0.525rem;
  line-height: 0.525rem;
  color: white;
  background: #4cd964;
  margin-top: 0.14rem;
  margin-left: 0.21rem;
  margin-right: 0.21rem;
  border-radius: 0.07rem;
}
.tip{
    padding-top: 0.18rem;
    margin-left: -140px;
    margin-top: -140px;
    background: white;
    text-align: center;
    position: fixed;
    top: 4.16rem;
    left: 2.34rem;
    right: 0.58rem;
}
.tip img{
    height: 1rem;
}
.tip_text{
    margin-top: 0.234rem; 
    padding: 0 0.117rem;
}
.confrim{
    background: #4CD964;
    color: white;
    height: 0.52rem;
    line-height: 0.52rem;
     margin-top: 0.234rem;
}
</style>



