import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Home from './../components/vue-cli/home'
import Msite from './../components/vue-cli/msite'
import Search from './../components/vue-cli/search'
import Order from './../components/vue-cli/order'
import Login from './../components/vue-cli/login'
import Shop from './../components/vue-cli/shop'
import Gotopay from './../components/vue-cli/gotopay'
import Mine from '../components/mine/mine'
import Download from '../components/mine/download'
import Service from '../components/mine/service'
import Content from '../components/mine/content'
import Member from '../components/mine/member'
import Integral from '../components/mine/integral'
import Detail from '../components/mine/detail'
import Balance from '../components/mine/balance'
import Detail1 from '../components/mine/detail1'
import Shopping from '../components/mine/shopping'
import Customer from '../components/mine/customer'
import History from '../components/mine/history'
import Exchange from '../components/mine/exchange'
import Payment from '../components/mine/payment'
import Privilege from '../components/mine/privilege'
import Info from "../components/mine/children/info"
import Packet from '../components/mine/packet'
import HbDescription from '../components/mine/hbDescription'
import Coupon from '../components/mine/coupon'
import HbHistory from '../components/mine/hbHistory'
import Exchange1 from '../components/mine/exchange1'
import Commend from '../components/mine/commend'
import Elm from '../components/city/eleMe'
import City from '../components/city/city'
import Address from '../components/mine/address'
import Add from '../components/mine/add'
import AddDetail from '../components/mine/addDetail'
import Forget from '../components/mine/forget'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Msite',
      component: Elm
    },{
      path: '/home',
      name: 'home',
      component: Home,
      children:[
        {
          path: 'msite',
          component: Msite
        },{
          path: 'search',
          component: Search
        },{
          path: 'order',
          component: Order
        },{
          path: 'mine',
          name:'Mine',
          component: Mine
        }
      ]
    },{
      path: '/login',
      name: 'login',
      component: Login
    },{
      path: '/shop/:id',
      name: 'shop',
      component: Shop
    },{
      path:'/gotopay',
      component:Gotopay
    },
    // {
    //   path: 'mine',
    //   name: Mine,
    //   component: Mine
    // },
    {
      path: '/elm',
      name: Elm,
      component: Elm
    },
    {
      path: '/city/:cityid',
      name: City,
      component: City
    },
    {
      path: '/mine/info', 
       name:"info",
       component: Info
    },{
      path: '/mine/download',
      name: 'Download',
      component: Download
    },
    {
      path: '/mine/service',
      name: 'Service',
      component: Service
    },
    {
      path: '/mine/member',
      name: 'Member',
      component: Member,
    },
    {
      path:'/mine/member/history',
      name: 'History',
      component: History
    },
    {
      path:'/mine/member/exchange',
      name: 'Exchange',
      component: Exchange
    },
    {
      path:'/mine/member/payment',
      name: 'Payment',
      component: Payment
    },
    {
      path:'/mine/member/privilege',
      name: 'Privilege',
      component: Privilege
    },
    {
      path: '/mine/integral',
      name: 'Integral',
      component: Integral
    },
    {
      path: '/mine/integral/detail',
      name: 'Detail',
      component: Detail
    },
    {
      path:'/mine/balance',
      name: 'Balance',
      component: Balance
    },
    {
      path: '/mine/balance/detail1',
      name: 'Detail1',
      component: Detail1
    },
    {
      path: '/mine/shopping',
      name: 'Shopping',
      component: Shopping
    },
    {
      path: '/mine/service/content',
      name: 'Content',
      component: Content
    },
    {
      path: '/mine/service/customer',
      name: 'Customer',
      component: Customer
    },
    {
      path: '/mine/packet',
      name: 'Packet',
      component: Packet
    },
    {
      path: '/mine/packet/hbDescription',
      name: 'HbDescription',
      component: HbDescription
    },
    {
      path: '/mine/packet/coupon',
      name: 'Coupon',
      component: Coupon
    },
    {
      path: '/mine/packet/hbHistory',
      name: 'HbHistory',
      component: HbHistory
    },
    {
      path: '/mine/packet/exchange1',
      name: 'Exchange1',
      component: Exchange1
    },
    {
      path: '/mine/packet/commend',
      name: 'Commend',
      component: Commend
    },
    {
      path: '/mine/info/address',
      name: 'Address',
      component: Address
    },
    {
      path: '/mine/info/address/add',
      name: 'Add',
      component: Add
    },
    {
      path: '/mine/info/address/add/addDetail',
      name: 'AddDetail',
      component: AddDetail
    },
    {
      path: '/forget',
      name: 'Forget',
      component: Forget
    }
  ]
})



