<template>
  <div id="content">
    <header id="head_top">
      <i class="el-icon-arrow-left" @click="routerback"></i>
      <span class="login_span">搜索</span>
    </header>
    <div class="search_form">
      <el-input v-model="text" class="search_input" placeholder="请输入商家或美食名称"></el-input>
      <el-button type="primary" class="search_submit" @click="submit">提交</el-button>
    </div>
    <div class="search_box" v-if="textData.length >= 1">
      <h4 class="title_restaurant">搜索历史</h4>
      <ul v-for="(item,index) in textData" :key="index">
        <li class="history_list">
          <span class="history_text">{{item.text}}</span>
          <span class="delete_icon" @click="deleteIcon(index,$event)">删除</span>
        </li>
      </ul>
      <footer class="clear_history" @click="deleteAll($event)">清空搜索历史</footer>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      textData: [],
      text: ""
    };
  },
  methods: {
    routerback: function() {
      this.$router.back(-1);
    },
    submit() {
      if (this.text == "") {
      } else if (this.text == " ") {
      } else {
        var obj = {};
        obj.text = this.text;
        this.textData.unshift(obj);
        this.text = " ";
      }
    },
    deleteIcon(index, ev) {
      if (this.textData.length == 1) {
        this.textData = [];
        this.text = "";
      } else {
        // console.log(index,ev.target);
        this.textData.splice(index, 1);
      }
    },
    deleteAll(ev) {
      this.textData = [];
      this.text = "";
    }
  }
};
</script>

<style scoped>
#content {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: rgb(245, 245, 245);
}
#head_top {
  width: 100%;
  height: 0.6rem;
  position: fixed;
  top: 0;
  left: 0;
  background: blue;
  z-index: 100;
}
.el-icon-arrow-left {
  color: white;
  font-size: 0.35rem;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}
.login_span {
  color: white;
  font-size: 0.25rem;
  display: inline-block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.search_form {
  width: 96%;
  height: 0.45rem;
  margin-top: 0.6rem;
  padding: 2%;
  background-color: white;
}
.search_input {
  width: 75%;
  height: 0.45rem;
  line-height: 0.45rem;
  font-size: 0.2rem;
}
.search_submit {
  width: 23%;
  height: 0.45rem;
}
.search_box {
  width: 100%;
}
.title_restaurant {
  width: 100%;
  height: 0.6rem;
  line-height: 0.6rem;
  font-size: 0.18rem;
  text-indent: 0.2rem;
  font-weight: bolder;
}
.history_list {
  width: 96%;
  height: 0.6rem;
  padding: 0 2%;
  background-color: white;
  border-bottom: 0.01rem solid #e4e4e4;
}
.history_text {
  width: 78%;
  height: 0.6rem;
  line-height: 0.6rem;
  font-size: 0.21rem;
  display: inline-block;
}
.delete_icon {
  width: 18%;
  height: 0.6rem;
  color: red;
  text-align: center;
  line-height: 0.6rem;
  font-size: 0.21rem;
  display: inline-block;
}
.clear_history {
  width: 100%;
  height: 0.6rem;
  line-height: 0.6rem;
  background-color: white;
  text-align: center;
  color: #3190e8;
  font-size: 0.21rem;
}
</style>


