<template>
    <div class="payment">
        <div class="payment-nav">
           <img @click='prev' src="./img/left.png">
           <span class="payment-span">在线支付</span>
       </div>
       <div class="payment-div">
           <span>支付剩余时间</span>
           <p>{{timer}}</p>
       </div>
       <div class="pay_way">选择支付方式</div>
       <div class="pay">
            <div class="zhi">
                <span><img height="60" src="./img/支付平台-支付宝.png">支付宝</span>
                <img class="img_img" v-show="!ads" @click="dtg" height="32" src="./img/选中.png">
                <img class="img_img" v-show="ads" @click="dtg"  height="32" src="./img/选中 (1).png">
            </div>
            <div class="wei">
                <span><img height="55" src="./img/微信.png">微信</span>
                <img class="img_img" v-show="!ade" @click="dtf" height="32" src="./img/选中 (1).png">
                <img class="img_img" v-show="ade" @click="dtf"  height="32" src="./img/选中.png">
            </div>
       </div>
       <div @click="teg" class="confirm">
           确定支付
       </div>
       <div v-show="t" class="tip">
           <img src="./img/感叹号.png">
           <p class="tip_text">当前环境无法支付，请打开官方APP进行付款</p>
           <router-link to="/home/order">
                <div class="confrim">确认</div>
           </router-link>
       </div>
    </div>
    
</template>

<script>
export default {
  name: "payment",
  data() {
    return {
        timer:'',
        ads:true,
        ade:true,
        t:false
    };
  },
  methods: {
    prev() {
      this.$router.go(-1);
    },
    dtg(){
        this.ads = true;
        this.ade = true;
    },
    dtf(){
        this.ads = false;
        this.ade = false;
    },
    teg(){
        this.t = true;
    }

  },
  beforeMount() {
    var _this = this;
    var to = setInterval(getTotelNumber, 1000);
    var newtime = 15 * 60;
    function getTotelNumber() {
      var m = Math.floor(newtime / 60);
      var s = Math.floor(newtime % 60);
      var msg1 = "00:" + m + ":" + s;
      --newtime;
      if (newtime >= 0) {
        _this.timer = msg1;
      } else {
        clearInterval(to);
        alert("支付超时，请重新支付");
      }
    }
    getTotelNumber();
  }

};
</script>

<style scoped>
.payment {
  background: #f5f5f5;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.payment-nav {
  width: 100%;
  height: 0.57rem;
  font-size: 0.25rem;
  background: #3190e8;
  z-index: 4;
  text-align: center;
  position: relative;
}
.payment-nav > img {
  height: 0.34rem;
  float: left;
  color: white;
  position: absolute;
  top: 50%;
  left: 1.5%;
  transform: translateY(-50%);
}
.payment-span {
  color: white;
  display: inline-block;
  font-weight: 550;
  line-height: 0.56rem;
}
.payment-div{
    padding: 0.21rem;
    text-align: center;
    background: white;
}
.payment-div span{
    display: inline-block;
    margin-top: 0.29rem;
    font-size: 0.18rem;
    height: 0.24rem;
    line-height: 0.24rem;
}
.payment-div p{
    margin-top: 0.09rem;
    margin-bottom: 0.29rem;
    font-size: 0.46rem;
}
.pay_way{
    height: 0.53rem;
    line-height: 0.53rem;
    padding-left: 0.205rem;
    padding-right: 0.205rem;
}
.zhi{
    height: 0.75rem;
    line-height: 0.75rem;
    background: white;
    padding: 0.12rem 0.21rem 0.12rem 0.21rem;
    position: relative;
}
.zhi span{
    display: flex;
    height: 0.75rem;
}
.wei{
     height: 0.75rem;
    line-height: 0.75rem;
    background: white;
    padding: 0.12rem 0.21rem 0.12rem 0.21rem;
    border: 0.01rem solid #f5f5f5;
    position: relative;
}
.wei span{
    display: flex;
    height: 0.75rem;
}
.wei span img{
    padding-left: 0.14rem; 
    padding-right: 0.14rem; 
}
.img_img{
    position: absolute;
    top: 0.27rem;
    right: 0.3rem;
}
.confirm{
    margin-top: 0.14rem;
    margin-left: 0.21rem;
    margin-right: 0.21rem; 
    height: 0.53rem;
    line-height: 0.53rem;
    text-align: center;
    background: #4CD964;
    color: white;
    border-radius: 0.04rem; 
}
.tip{
    padding-top: 0.18rem;
    margin-left: -140px;
    margin-top: -140px;
    background: white;
    text-align: center;
    position: fixed;
    top: 4.16rem;
    left: 2.34rem;
    right: 0.58rem;
}
.tip img{
    height: 1rem;
}
.tip_text{
    margin-top: 0.234rem; 
    padding: 0 0.117rem;
}
.confrim{
    background: #4CD964;
    color: white;
    height: 0.52rem;
    line-height: 0.52rem;
     margin-top: 0.234rem;
}
</style>


