<template>
    <div class="coupon">
        <div class="coupon-nav">
           <img @click='prev' src="./img/left.png">
           <span class="coupon-span">代金券说明</span>
       </div>
       <div class="coupon-div">
           <div  class="markdown">
               <h3>Q1: 什么是商家代金券？</h3> 
               <p >商家代金券是指由商家自己发放代金券，只限在指定的商家使用，可根据条件抵扣相应金额。</p> 
               <h3>Q2: 怎么获得商家代金券？</h3> 
               <ul >
                   <li >进入有「进店领券」或「下单返券」标示的商家即有机会获得代金券。</li> 
                   <li >「下单返券」需要在指定商家完成满足返券金额要求的订单后才会返还，代金券可在下次下单时使用。</li>
                </ul> 
                <h3>Q3: 商家代金券使用条件</h3> 
                <ul >
                    <li >商家代金券仅限在指定商家使用</li> 
                    <li >商家代金券仅限在线支付使用</li> 
                    <li >每个订单只能使用一张商家代金券，不可与其他代金券叠加使用</li>
                </ul>
            </div>
       </div>
    </div>
</template>

<script>
export default {
  methods: {
    prev() {
      this.$router.go(-1);
    }
  }
};
</script>

<style scoped>
.coupon {
  background: #ffffff;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.coupon-nav {
  width: 100%;
  height: 0.57rem;
  font-size: 0.25rem;
  background: #3190e8;
  z-index: 4;
  text-align: center;
  position: fixed;
}
.coupon-nav > img {
  height: 0.34rem;
  float: left;
  color: white;
  position: absolute;
  top: 50%;
  left: 1.5%;
  transform: translateY(-50%);
}
.coupon-span {
  color: white;
  display: inline-block;
  font-weight: 550;
  line-height: 0.56rem;
}
.coupon-div{
    padding: 0.14rem;
    padding-top: 0; 
    margin-top: 0.57rem;
    font-size: 0.16rem;
}
.coupon-div h3{
    display: inline-block;
    height: 0.57rem;
    line-height: 0.57rem;
    font-size: 0.2rem;
}
.coupon-div li {
     display: inline-block;
     height: 0.24rem;
     line-height: 0.24rem;
}
.coupon-div p {
     display: inline-block;
     height: 0.24rem;
     line-height: 0.24rem;
}
</style>


