<template>
    <div class="history">
        <div class="history-nav">
           <img @click='prev' src="./img/left.png">
           <span class="history-span">我的优惠</span>
       </div>
       <div class="history-div">
           <img src="./img/积分.png">
           <span>没有购买记录</span>
       </div>
    </div>
</template>

<script>
export default {
  name: "history",
  data() {
    return {};
  },
  methods: {
    prev() {
      this.$router.go(-1);
    }
  }
};
</script>

<style scoped>
.history {
  background: white;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.history-nav {
  width: 100%;
  height: 0.57rem;
  font-size: 0.25rem;
  background: #3190e8;
  z-index: 4;
  text-align: center;
  position: relative;
}
.history-nav > img {
  height: 0.34rem;
  float: left;
  color: white;
  position: absolute;
  top: 50%;
  left: 1.5%;
  transform: translateY(-50%);
}
.history-span {
  color: white;
  display: inline-block;
  font-weight: 550;
  line-height: 0.56rem;
}
.history-div{
    text-align: center;
}
.history-div img{
    width:2.38rem;
    height:1.45rem;
    margin-top: 1.47rem;
}
.history-div span{
    display: block;
    margin-top: 0.23rem;
    font-size: 0.18rem;
    color: #999999;
}
</style>


