<template>
    <body>
        <div id="container">
            <div class="description" :style="'background-image:url(http://elm.cangdu.org/img/'+data.image_path+')'">
                <nav class="goback" @click="goback"><i class="el-icon-arrow-left" style="color:white;font-size:0.3rem"></i></nav>
                <section class="description_header">
                    <router-link to="/" class="description_top">
                        <section class="description_left">
                            <img :src="'http://elm.cangdu.org/img/'+data.image_path" alt="">
                        </section>
                        <section class="description_right">
                            <p class="description_name">{{data.name}}</p>
                            <p class="description_text">商家配送/分钟必达/配送费￥5</p>
                            <p class="description_promotion">公告:{{data.promotion_info}}</p>
                        </section>
                    </router-link>
                    <footer class="description_footer" v-if="activities">
                        <span class="tip_icon" :style="'background-color:#'+activities.icon_color">{{activities.icon_name}}</span>
                        <span style="color:white">{{activities.description}}(APP专属)</span>
                        <span style="color:white;float:right;">一个活动 ></span>
                    </footer>
                </section>
                <div class="change_show_type">
                <div><span :class="{'activity_show':isclass}" @click="toggle($event)">商品</span></div>
                <div><span :class="{'activity_show':!isclass}" @click="toggle($event)">评价</span></div>
            </div>
            </div>
            <div class="menu" v-if="isclass">
                <div class="menu_left">
                    <ul class="menu_left_ul">
                        <li class="menu_left_li" v-for="(item,index) in menu" :key="index" @click="menuChange(index)" :class="{ active: iscur == index }">
                            <span>{{item.name}}</span>
                        </li>
                    </ul>
                </div>
                <div class="menu_right" v-for="(item,index) in menu" :key="index">
                    <header class="menu_detail_header">
                        <strong style="font-size:0.24rem;font-weight:bloder">{{item.name}}</strong>
                        <span style="color:gray">{{item.description}}</span>
                    </header>
                    <ul>
                        <li class="menu_detail_list" v-for="(list,index) in item.foods" :key="index">
                            <div class="menu_food_img">
                                <img :src="'http://elm.cangdu.org/img/'+list.image_path" alt="">
                            </div>
                            <div class="menu_food_right">
                              <h3 class="food_description_head">
                                <strong class="description_foodname">{{list.name}}</strong>
                                <ul v-if="list.attributes" class="attributes_ul">
                                  <li v-for="(attr,index) in list.attributes" :key="index" v-if="attr">
                                    <span v-if="attr.icon_name=='招牌'" :style="'color:#'+attr.icon_color+';border:0.01rem solid #'+attr.icon_color" class="attributes_span">{{attr.icon_name}}</span>
                                  </li>
                                </ul>
                              </h3>
                              <p class="food_description_content">{{list.description}}</p>
                              <p class="food_description_sale_rating">月销{{list.month_sales}}份 好评率{{list.satisfy_rate}}%</p>
                              <p class="food_activity">
                                 <span v-if="list.activity" :style="'color:#'+list.activity.image_text_color+';border:0.01rem solid #'+list.activity.icon_color">{{list.activity.image_text}}</span> 
                                 </p>
                              <footer class="menu_detail_footer">
                                <span>￥</span>
                                <span v-for="(spec,index) in list.specfoods" :key="index" v-if="index==0">{{spec.price}}</span>
                                <span class="add_icon" @click="addIcon(list.specfoods[0])">+</span>
                                <span class="stateCount">{{list.specfoods[0].count}}</span>
                                <span class="reduce_icon" @click="reduceIcon(list.specfoods[0])">-</span>
                              </footer>
                            </div>
                        </li>
                    </ul>
                </div>
            </div>
            <div class="cart_icon_num">
              <div class="cart_icon_activity">
                <span class="cart_list_length">{{$store.state.count}}</span>
              </div>
              <div class="cart_num">
                <div style="height:0.4rem;line-height:0.4rem;font-weight:bolder;font-size:0.25rem">￥{{$store.state.count2}}.00</div>
                <div style="font-size:0.1rem">配送费￥5</div>
              </div>
              <router-link to="/gotopay" class="gotopay">去结算</router-link>
                     
            </div>
        </div>
    </body>
</template>

<script>
import Vue from "vue";
export default {
  data() {
    return {
      data: [],
      activities: [],
      menu: [],
      isclass: true,
      iscur:0
    };
  },
  created() {
    let api =
      "https://elm.cangdu.org/shopping/restaurant/" + this.$route.params.id;
    this.$http.get(api).then(data => {
      this.data = data.data;
      this.activities = data.data.activities[0];
    });
    let api1 =
      "https://elm.cangdu.org/shopping/v2/menu?restaurant_id=" +
      this.$route.params.id;
    this.$http.get(api1).then(data => {
      for (let i = 0; i < data.data.length; i++) {
        for (let ii = 0; ii < data.data[i].foods.length; ii++) {
          Vue.set(data.data[i].foods[ii].specfoods[0], "count", 0);
        }
      }
      this.menu = data.data;
      console.log(this.menu);
    });
  },
  computed:{

  },
  methods: {
    goback() {
      this.$router.back(-1);
    },
    toggle(e) {
      if (e.target.className == "activity_show") {
        this.isclass = this.isclass;
      } else {
        this.isclass = !this.isclass;
      }
    },
    menuChange(index){
      // console.log(index);
      this.iscur = index;
    },
    addIcon(a){
      this.$store.commit("add", a);
    }
  }
};
</script>

<style scoped>
body {
  background-color: rgb(245, 245, 245);
  overflow: hidden;
}
.description {
  width: 100%;
  background-size: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
  overflow: hidden;
}
.goback {
  width: 100%;
  height: 0.5rem;
  background-color: transparent;
  position: fixed;
  top: 0;
  left: 0;
}
.description_header {
  width: 98%;
  /* height: 0.9rem; */
  padding: 1% 0 1% 1%;
  background-color: rgba(119, 103, 137, 0.53);
}
.description_top {
  display: flex;
}
.description_left {
  width: 0.9rem;
  height: 0.9rem;
  margin-right: 0.05rem;
}
.description_left > img {
  width: 0.85rem;
  height: 0.85rem;
  display: block;
  border-radius: 0.1rem;
}
.description_name {
  font-size: 0.23rem;
  margin-bottom: 0.15rem;
  color: white;
  font-weight: bolder;
}
.description_text {
  font-size: 0.15rem;
  margin-bottom: 0.15rem;
  color: white;
}
.description_promotion {
  font-size: 0.15rem;
  /* margin-bottom: 0.2rem; */
  color: white;
}
.description_footer {
  margin-top: 0.12rem;
}
.tip_icon {
  width: 0.2rem;
  height: 0.2rem;
  display: inline-block;
  border-radius: 0.05rem;
  color: white;
  line-height: 0.2rem;
  text-align: center;
  font-size: 0.1rem;
}
.change_show_type {
  width: 100%;
  height: 0.41rem;
  padding: 0.1rem 0 0.2rem 0;
  display: flex;
  background-color: white;
}
.change_show_type > div {
  width: 50%;
  height: 0.41rem;
  text-align: center;
  line-height: 0.41rem;
}
.change_show_type span {
  margin-top: 0.1rem;
  display: inline-block;
}
.activity_show {
  color: #3190e8;
  border-bottom: 0.05rem solid #3190e8;
}
.menu {
  width: 100%;
  margin-top: 2.04rem;
}
.menu_left {
  width: 25%;
  float: left;
}
.menu_right {
  width: 75%;
  float: right;
}
.menu_left_li {
  position: relative;
  width: 80%;
  height: 0.38rem;
  padding: 0.2rem 0.1rem;
  border-left: 0.05rem solid transparent;
  /* background-color: pink; */
}
.active{
    border-left: 0.05rem solid #3190e8;
    background-color: white;
  /* background-color: pink; */
}
.menu_left_li > span {
  width: 70%;
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.menu_detail_header {
  width: 94%;
  height: 0.4rem;
  padding: 0.12rem 3%;
  line-height: 0.4rem;
  background-color: lightgray;
}
.menu_detail_list {
  width: 94%;
  height: 1.33rem;
  padding: 0.17rem 3%;
  border-bottom: 0.01rem solid #f8f8f8;
  background-color: white;
}
.menu_food_img{
    width: 18%;
    height: 0.75rem;
    margin-right: 2%;
    float: left;
}
.menu_food_img img{
    width: 100%;
}
.menu_food_right{
  width: 80%;
  float: right;
  height: 0.75rem;
}
.food_description_head{
  height: 0.28rem;
  margin-bottom: 0.05rem;
}
.attributes_ul{
  font-size: 0.1rem;
  float: right;
}
.attributes_span{
  border-radius: 0.2rem;
}
.description_foodname{
  width: 100%;
  font-size: 0.2rem;
  font-weight: bolder;
}
.food_description_content{
  height: 0.2rem;
  line-height: 0.2rem;
  font-size: 0.15rem;
  color: #999;
}
.food_description_sale_rating{
  height: 0.3rem;
  line-height: 0.3rem;
  font-size: 0.15rem;
}
.food_activity{
  height: 0.2rem;
}
.food_activity span{
  height: 0.198rem;
  line-height: 0.198rem;
  font-size: 0.15rem;
  border-radius: 0.1rem;
}
.cart_icon_num{
  width: 100%;
  height: 0.6rem;
  background-color: #999;
  position: fixed;
  bottom: 0;
  left: 0;
  z-index: 100;
}
.cart_icon_activity{
  width: 0.6rem;
  height: 0.6rem;
  border-radius: 50%;
  background-color: skyblue;
  position: absolute;
  top: -0.1rem;
  left: 0.2rem;
}
.cart_list_length{
  width: 0.2rem;
  height: 0.2rem;
  font-size: 0.1rem;
  text-align: center;
  line-height: 0.2rem;
  display: block;
  border-radius: 50%;
  color: white;
  background-color: red;
  position: absolute;
  top: 0;
  right: 0;
}
.reduce_icon,.stateCount,.add_icon{
  width: 0.25rem;
  height: 0.25rem;
  border-radius: 50%;
  text-align: center;
  font-size: 0.3rem;
  line-height: 0.25rem;
  display:inline-block;
  float:right;
  background-color: skyblue;
  color: white;
}
.stateCount{
  font-size: 0.1rem;
}
.cart_num{
  width: 0.82rem;
  height: 0.6rem;
  color: white;
  position: absolute;
  top: 0;
  left: 1rem;
}
.gotopay{
  width: 1.45rem;
  height: 0.6rem;
  text-align: center;
  line-height: 0.6rem;
  color: white;
  background-color: yellowgreen;
  position: absolute;
  top: 0;
  right: 0;
}
</style>




