<template>
    <div class="commend">
        <div class="commend-nav">
           <img @click='prev' src="./img/left.png">
           <span class="commend-span">推荐有奖</span>
       </div>
        <div class="img">
            <img src="./img/DA.png" alt="">
        </div>
           <ul class='last'>
               <li>
                   <img src="./img/WEI.png" alt="">
                   <p>邀请微信好友</p>
               </li>
               <li>
                   <img src="./img/QQ.png" alt="">
                   <p>邀请QQ好友</p>
               </li>
               <li>
                   <img src="./img/MIAN.png" alt="">
                   <p>面对面邀请</p>
               </li>
           </ul>
           <ul class="invitation">
               <li>
                   <p>累计收益</p>
                   <div>
                       <span class="leftw">0</span>
                       <span>元</span>
                   </div>
               </li>
               <li>
                   <p>成功邀请</p>
                   <div>
                       <span class="rightw">0</span>
                       <span>人</span>
                   </div>
               </li>
           </ul>
           <div class='bottom'>
              <p>-收益明细-</p>
              <img src="./img/BAO.png" alt="">
              <p>还不快去邀请好友</p>
           </div>
           
    </div>
</template>

<script>
export default {
  data() {
    return {
    };
  },
   methods: {
    prev() {
      this.$router.go(-1);
    },
   }

};
</script>
<style scoped='scoped'>
.commend {
  background: #f5f5f5;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.commend-nav {
  width: 100%;
  height: 0.57rem;
  font-size: 0.25rem;
  background: #3190e8;
  z-index: 4;
  text-align: center;
  position: relative;
}
.commend-nav > img {
  height: 0.34rem;
  float: left;
  color: white;
  position: absolute;
  top: 50%;
  left: 1.5%;
  transform: translateY(-50%);
}
.commend-span {
  color: white;
  display: inline-block;
  font-weight: 550;
  line-height: 0.56rem;
}
.img {
  height: 2.3438rem;
}
.img img {
  width: 100%;
  height: 100%;
}
.last {
  background-color: white;
  padding: 0.15rem;
  display: flex;
}
.last li {
  width: 100%;
  text-align: center;
}
.last li img {
  width: 0.5859rem;
  height: 0.5859rem;
}
.last li p {
  margin-top: 0.1rem;
  font-size: 0.13rem;
}
.invitation {
  display: flex;
  margin: 0.2rem 0;
}
.invitation li {
  width: 100%;
  text-align: center;
  font-size: 0.13rem;
}
.invitation li p {
  margin-bottom: 0.1rem;
}
.leftw {
  color: red;
  font-size: 0.16rem;
  font-weight: bold;
}
.rightw {
  color: black;
  font-size: 0.16rem;
  font-weight: bold;
}
.invitation li:first-child {
  border-right: 0.01rem solid rgb(209, 199, 199);
}
.bottom {
  margin-top: 1rem;
  text-align: center;
  font-size: 0.16rem;
}
.bottom img {
  width: 0.3rem;
}
.bottom p {
  margin: 0.2rem 0;
}
</style>


