<template>
    <div class="exchange">
        <div class="exchange-nav">
           <img @click='prev' src="./img/left.png">
           <span class="exchange-span">兑换会员</span>
       </div>
       <p class="exchange-p">成功兑换后将关联到当前帐号: <span>{{username}}</span></p>
       <div class="import">
           <input class="import1" type="text" placeholder="请输入10位卡号"><br>
           <input class="import1" type="text" placeholder="请输入6位卡密">
       </div>
       <div class="click">
           兑换
       </div>
       <div class="prompt">
           <h3>——温馨提示——</h3>
           <p>新兑换的会员服务，权益以「会员说明」为准。</p>
           <p>月卡：<b>30次</b>减免配送费。</p>
           <p>季卡：<b>90次</b>减免配送费。</p>
           <p>年卡：<b>360</b>次减免配送费。</p>
           <p>＊仅限蜂鸟专送订单，每日最多减免3单，每单最高减免4元（一个月按31天计算）</p>
       </div>
    </div>
</template>

<script>
export default {
name: "exchange",
  data() {
    return {
        username:""
    };
  },
  methods: {
    prev() {
      this.$router.go(-1);
    }
  },
  computed:{
     bol(){
      if(localStorage.obj!==""){
        return true;
      }else{
        return false;
      }
    }
  },
  created(){
   
       this.username= this.$store.state.userInfo.username;
  }
};
</script>

<style scoped>
.exchange {
  background: #f5f5f5;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.exchange-nav {
  width: 100%;
  height: 0.57rem;
  font-size: 0.25rem;
  background: #3190e8;
  z-index: 4;
  text-align: center;
  position: relative;
}
.exchange-nav > img {
  height: 0.34rem;
  float: left;
  color: white;
  position: absolute;
  top: 50%;
  left: 1.5%;
  transform: translateY(-50%);
}
.exchange-span {
  color: white;
  display: inline-block;
  font-weight: 550;
  line-height: 0.56rem;
}
.exchange-p{
    display: inline-block;
  height: 0.58rem;
  padding-left: 0.21rem;
  line-height: 0.58rem;
  font-size: 0.18rem;
}
.exchange-p span{
  font-size: 0.18rem;
  font-weight: 600;
}
.import1{
    width: 4.482rem;
    height: 0.57rem;
    padding-left: 0.21rem;
    outline: none;
    font-size: 0.2rem;
}
.click{
    margin: 0.21rem;
    margin-bottom: 0;
    height: 0.53rem;
    background: #CCCCCC;
    line-height: 0.53rem;
    text-align: center;
    color: white;
    font-size: 0.22rem;
    border-radius: 0.07rem;
}
.prompt{
    margin-top: 0.292rem;
}
.prompt h3{
    text-align: center;
    margin-bottom: 0.18rem;
}
.prompt p {
    padding-left: 0.58rem;
    font-size: 0.16rem;
    height: 0.23rem;
    display: inline-block;
    line-height: 0.23rem;

}
</style>


