<template>
    <div id="loginContainer">
        <header id="head_top">
            <i class="el-icon-arrow-left" @click="routerback"></i>
            <span class="login_span">密码登录</span>
        </header>
        <div class="inputs">
            <div class="input_container">
                <input type="text" v-model.lazy="userAccount" class="search_input" placeholder="账号">
            </div>
            <div class="input_container">
                <input :type="value==true?'text':'password'" class="search_input" v-model="passWord" placeholder="密码">
                <el-switch v-model="value" active-text="" inactive-text="" class="switch">
                </el-switch>
            </div>
            <div class="input_container">
                <input type="text" v-model="codeNumer" class="search_input" placeholder="验证码">
                <img @click="getCode()" :src="code&&code" class="login_img">
                <div class="change_img">
                  <p class="img_p1" @click="getCode()">看不清</p>
                  <p class="img_p2" @click="getCode()">换一张</p>
                </div>
            </div>
        </div>
        <div>
        </div>
        <div class="login_bottom">
      <p class="login_p1">
        温馨提示：未注册过的账号，登录时将自动注册
      </p>
      <p class="login_p2">
        注册过的用户可凭账号密码登录
      </p>

    </div>
    <el-button type="success login_button" @click="login">登录</el-button>
    <router-link to="/forget" class="login_forget">重置密码？</router-link>
    </div>
</template>

<script>
export default {
  data() {
    return {
      value: true,
      code: "",
      passWord: "",
      codeNumer: "",
      phoneNumber: null, //电话号码
      userInfo: null, //获取到的用户信息
      userAccount: null, //用户名
      username: "",
      value1: true,
      value2: true
    };
  },
  created() {
    this.getCode();
  },
  methods: {
    routerback: function() {
      this.$router.back(-1);
    },
    changeText() {
      if (this.value) {
        this.type = text;
      } else {
        this.type = password;
      }
    },
    getCode() {
      this.axios({
        method: "post",
        url: 'https://elm.cangdu.org/v1/captchas',
        withCredentials: true // 默认false
      }).then(res => {
        if (res.data.status == 200) {
        }
        this.code = res.data.code;
      });
    },
    login() {
      this.axios({
        method: "post",
        url: 'https://elm.cangdu.org/v2/login',
        withCredentials: true,
        data: {
          captcha_code: this.codeNumer,
          password: this.passWord,
          username: this.userAccount
        }
      }).then(res => {
        if (res.data.status == 0) {
          alert(res.data.message);
          this.$router.go(0);
        } else {
           this.$store.commit("setusername", this.userAccount);
          this.$router.push({ name: "Mine" });
          JSON.stringify(localStorage.setItem('obj',res.data));
          this.$store.commit('user',res.data)
          
        }
        console.log("---", res);
      });
    }
  },
  computed: {
    //判断
    rightPhoneNumber: function() {
      return /^[A-Za-z0-9]{4,16}$/;
    },
    setusername() {
      return this.$store.state.username;
    }
  },
};
</script>

<style  scoped='scoped'>
#loginContainer {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: rgb(245, 245, 245);
}
#head_top {
  width: 100%;
  height: 0.6rem;
  position: fixed;
  top: 0;
  left: 0;
  background: #3190e8;
  z-index: 100;
}
.el-icon-arrow-left {
  color: white;
  font-size: 0.35rem;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}
.login_span {
  color: white;
  font-size: 0.25rem;
  display: inline-block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.inputs {
  width: 100%;
  height: 1.85rem;
  background-color: white;
  margin-top: 0.75rem;
}
.input_container {
  width: 90%;
  height: 0.25rem;
  padding: 0.18rem 0.23rem;
  position: relative;
}
.search_input {
  width: 50%;
  height: 0.25rem;
  font-size: 0.25rem;
  line-height: 0.25rem;
  border: 0px;
  outline: none;
}
.login_img {
  position: absolute;
  top: 30%;
  right: 20%;
}
.switch {
  float: right;
}
.change_img {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 0.2rem;
}
.login_bottom {
  position: absolute;
  width: 100%;
  top: 3rem;
  left: 0;
}
.login_p1 {
  color: red;
  font-size: 0.13rem;
  padding-left: 0.2rem;
  margin-bottom: 0.1rem;
}
.login_p2 {
  color: red;
  font-size: 0.13rem;
  padding-left: 0.2rem;
}
.login_button {
  position: absolute;
  width: 92%;
  top: 3.6rem;
  left: 50%;
  transform: translateX(-50%);
  padding-left: 0.2rem;
  font-size: 0.2rem;
}
.login_forget {
  position: absolute;
  top: 4.3rem;
  right: 0.2rem;
  font-size: 0.18rem;
  color: rgb(75, 157, 234);
}
</style>


